 // a:not([href]):not([tabindex])
.btn, a:not([href]):not([tabindex]):not([data-ng-attr-id]) {
  background: $grey;
  font-size: $font-normal;  
  font-weight: 500;
  color: $grey-dark;
  -webkit-transition: opacity .2s ease-in-out, background .2s ease-in-out, box-shadow .2s ease-in-out;
  -o-transition: opacity .2s ease-in-out, background .2s ease-in-out, box-shadow .2s ease-in-out;
  transition: opacity .2s ease-in-out, background .2s ease-in-out, box-shadow .2s ease-in-out;
  &.btn--lg { @include padding-mixin(0.5rem 3rem) } 
  &.btn--green, {
    background: $green;
    color: $white;
  }  
  &.btn--red {
    background: $red;
    color: $white;
  }
  &:not([disabled]):hover, &:not([disabled]):focus  {
    box-shadow: 0 0 2px $grey-dark;
  }

}

.btn-underlined-selector { //required for addtocart buttons in table. Rest styles
  .btn {
    text-decoration: underline;
    border: 0;
    background: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    &.cartbtn {
    }
    &.cartbtn::after {
      -webkit-transition: color 12s ease-in-out, content 12s ease-in-out, font-size 12s ease-in-out, text-shadow 12s ease-in-out;
      -o-transition: color 12s ease-in-out, content 12s ease-in-out, font-size 12s ease-in-out, text-shadow 12s ease-in-out;
      transition: color 12s ease-in-out, content 12s ease-in-out, font-size 12s ease-in-out, text-shadow 12s ease-in-out;
      display: inline-block;
      font-size: 2rem;
      font-family: "Font Awesome 5 Free"; font-weight: 900;
      text-decoration: none;
      margin: 0 0 0 10px;
      vertical-align:middle;
    }
    &.cartbtn.add::after {
      content: "\f217";
      color: $green;      
    }
    &.cartbtn.remove::after {
      content: "\f218";
      color: $red;      
    }    
    &.cartbtn:hover::after {
      font-size: 2.1rem;
      text-shadow: 0 0 2px $grey;
    }
    &:focus {
      outline: 0;
    }
    &:hover {
      text-decoration: none;
    }
    &.btn--green, &.btn--red {
      color: $black;
      background: none;
    }
    &:not([disabled]):hover, &:not([disabled]):focus  {
      box-shadow: none;
    }    
  }
}

