.t-data .t-data__btn-portal { min-width: 183.48px; } //required from ngCart

.btn-no-bg {
  background: none;
  border: 0;
  &:focus {
    outline: 0;
  }
}

.btn-underlined {
  text-decoration: underline;
  &:hover {
    text-decoration: none;
  }
}

.btn--bordered {
  background: none;
  &.green {
    border: 2px solid $green;
    color: $green;
    &:not([disabled]):hover {
      background: $green;
      color: $white;
    }
    &.txt-black {
      color: $black;
    }
  }  
}


